@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
.b-bg-main{
	width: 100%;
	height: 100%;
	background: $color-bg1;
	position: fixed;
	top: 0;
	left: 0;
	&__build{
		width: 100%;
		height: 100%;
		background: left center url(../images/svg/build.svg) no-repeat;
		background-size: 100% auto;
		transition: all 0.3s ease;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
	}
	&__man{
		
	}
	&__flower{
		width: 100%;
		height: 100%;
		background: left center url(../images/svg/flower.svg) no-repeat;
		background-size: 100% auto;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 3;
	}
}
.b-screen{
	position: relative;
	overflow: hidden;
	@extend .b-flex;
	@extend .b-flex--middle-col;
	.b-center{
		width: 1040px;
		height: 626px;
		margin: 0 auto;
		position: relative;
	}
	.b-dialog{
		position: absolute;
	}
	&__img{
		display: block;
		background-position: 0 0;
		background-repeat: no-repeat;
		background-size: 100% auto;
		position: absolute;
		&--buildL,
		&--buildR{
			width: calc(50% - 20px);
			height: 404px;
			background-image: url(../images/svg/build.svg);
			background-color: $color-bg2;
			background-position: left bottom;
			top: 0;
			z-index: 1;
		}
		&--buildL{
			left: 0;
		}
		&--buildR{
			right: 0;
		}
		&--man{
			width: 173px;
			height: 420px;
			background-image: url(../images/svg/man.svg);
			top: calc(50% - 214px);
			left: calc(50% - 86px);
			z-index: 5;
		}
		&--flower{
			width: 100%;
			top: calc(50% + 90px);
			left: 0;
			z-index: 2;
			&:before{
				width: 100%;
				height: 8px;
				display: block;
				content: '';
				background: $color-white;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 1;
			}
			span{
				width: 61px;
				height: 144px;
				display: block;
				background-image: url(../images/svg/flower.svg);
				background-position: 0 0;
				background-repeat: no-repeat;
				background-size: 100% auto;
				position: absolute;
				top: calc(50% - 114px);
				right: 12px;
				&:first-child{
					right: auto;
					left: 12px;
				}
			}
		}
		&--ipad,
		&--ipad2{
			width: 71px;
			height: 98px;
			background-image: url(../images/svg/ipad.svg);
			top: calc(50% - 82px);
			left: calc(50% + 46px);
		}
		&--ipad2{
			background-image: url(../images/svg/ipad2.svg);
			.b-dialog{
				bottom: calc(100% + 43px);
				left: 2px;
			}
		}
		&--table{
			width: 646px;
			height: 282px;
			background-image: url(../images/svg/table.svg);
			bottom: 0;
			left: calc(50% - 320px);
			z-index: 5;
			.b-dialog{
				bottom: calc(100% + 31px);
				left: calc(100% - 54px);
			}
		}
		&--calendar{
			width: 250px;
			height: 355px;
			background-image: url(../images/svg/calendar.svg);
			top: calc(50% - 206px);
			left: calc(50% - 432px);
			z-index: 1;
			.b-dialog{
				bottom: 100%;
				right: calc(100% - 110px);
			}
		}
		&--lamp{
			width: 139px;
			height: 457px;
			background-image: url(../images/svg/lamp.svg);
			background-position: left bottom;
			bottom: calc(100% - 69px);
			right: calc(50% - 458px);
			z-index: 5;
			&:before{
				width: 2px;
				height: 5000px;
				display: block;
				content: '';
				background: #7f7f81;
				position: absolute;
				bottom: 100px;
				left: calc(50% - 1px);
			}
			.b-dialog{
				bottom: 117px;
				left: calc(100% - 65px);
			}
		}
		&--calculator{
			width: 40px;
			height: 50px;
			background-image: url(../images/svg/calculator.svg);
			top: calc(50% - 43px);
			left: calc(50% + 52px);
		}
		&--board{
			width: 207px;
			height: 453px;
			background-image: url(../images/svg/board.svg);
			top: calc(50% - 313px);
			left: calc(50% + 135px);
			z-index: 2;
			.b-dialog{
				top: 96px;
				left: calc(100% + 22px);
			}
		}
		&--mirror{
			width: 399px;
			height: 280px;
			background-image: url(../images/svg/mirror.svg);
			bottom: calc(100% - 220px);
			right: calc(100% - 416px);
			z-index: 2;
			.b-dialog{
				bottom: calc(100% - 90px);
				left: calc(100% - 60px);
			}
		}
		&--photo{
			width: 152px;
			height: 150px;
			background-image: url(../images/svg/photo.svg);
			bottom: calc(100% - 52px);
			right: calc(100% - 221px);
			.b-dialog{
				bottom: 26px;
				left: calc(100% + 15px);
			}
		}
		&--certified{
			width: 152px;
			height: 158px;
			background-image: url(../images/svg/certified.svg);
			bottom: calc(100% - 228px);
			right: calc(100% - 221px);
			.b-dialog{
				top: 0;
				right: calc(100% - 15px);
			}
		}
		&--strategy{
			width: 248px;
			height: 420px;
			background-image: url(../images/svg/man.svg);
			top: calc(50% - 214px);
			left: calc(50% - 86px);
			z-index: 5;
			.b-dialog{
				bottom: calc(50% + 71px);
				right: calc(100% - 16px);
			}
		}
		&--graph{
			width: 356px;
			height: 342px;
			background-image: url(../images/svg/graph.svg);
			bottom: calc(100% - 277px);
			left: calc(100% - 254px);
			.b-dialog{
				bottom: calc(50% + 14px);
				right: calc(100% + 5px);
			}
		}
	}
	&--main{
		width: 100%;
		height: 100%;
		background: $color-bg1;
		position: fixed;
		top: 0;
		left: 0;
	}
	&--two{}
	&--four{}
}
.fp-viewing-slideFour{
	.b-screen{
		&__img{
			&--buildL,
			&--buildR{
				opacity: 0 !important;
			}
		}
		&--main{
			background: $color-bg2;
		}
	}
}

.b-btn-arrow{
    width: 40px;
    height: 40px;
    display: block;
    background: $color-green;
    border-radius: 100%;
    transition: all 0.25s ease;
    position: relative;
    z-index: 30;
    &:before{
        width: 40px;
        height: 40px;
        display: block;
        content: '';
        background: $color-green; 
        box-shadow: 0 0 0 4px $color-green;
        border-radius: 100%; 
        animation: btn 1.5s linear infinite;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
    }
    &:after{
        width: 20px;
        height: 20px;
        margin: -10px 0 0 -10px;
        display: block;
        content: '';
        background-image: url(../images/svg/arrow.svg);
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 2;
    }
}
.b-dialog{
    width: 200px;
    height: 126px;
    padding: 10px 35px 62px;
    background: $color-white;
    border-radius: 100px / 63px;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    line-height: 17px;
    color: $color-body;
    text-align: center;
    position: relative;
    z-index: 99;
    @extend .b-flex;
    @extend .b-flex--middle-col;
    &:before,
    &:after{
        display: block;
        content: '';
        transform: rotate(45deg);
        position: absolute;
        bottom: -20px;
    }
    &:before{
        width: 0;
        height: 0;
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;
        border-top: 40px solid $color-white;
        transform: rotate(45deg);
        left: 35px;
        z-index: 1;
    }
    &:after{
        width: 2px;
        height: 40px;
        background: $color-white;
        left: 50px;
        z-index: 2;
    }
    &--right{
        &:before,
        &:after{
            transform: rotate(-45deg);
            left: auto;
        }
        &:before{
            right: 35px;
        }
        &:after{
            right: 50px;
        }
    }
    &--center{
        &:before{
            border-left: none;
            border-top: 10px solid transparent;
            border-bottom: 20px solid transparent;
            border-right: 40px solid $color-white;
            transform: rotate(0deg);
            bottom: calc(50% - 20px);
            left: -23px;
        }
        &:after{
            display: none;
        }
    }
    .b-btn-arrow{
        margin-left: -20px;
        position: absolute;
        bottom: 16px;
        left: 50%;
    }
}
.b-entry{
    height: 276px;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 30px;
    line-height: 34px;
    color: $color-black;
    text-align: center;
}
.b-tit{
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 36px;
    font-weight: normal;
    line-height: 40px;
    span,
    strong{
        color: $color-green;
    }
    span{
        display: block;
    }
}
.popover{
	.img{
		&--photo{
			width: 155px;
			height: 149px;
			background-image: url(../images/svg/photo-popover.svg);
			bottom: calc(100% - 20px);
			left: 45px;
		}
		&--certified{
			width: 153px;
			height: 155px;
			background-image: url(../images/svg/certified-popover.svg);
			bottom: calc(100% - 26px);
			left: 26px;
		}
		&--strategy{
			width: 81px;
			height: 108px;
			background-image: url(../images/svg/strategy-popover.svg);
			bottom: calc(100% - 70px);
			right: calc(100% - 28px);
		}
		&--graph{
			width: 123px;
			height: 120px;
			background-image: url(../images/svg/graph-popover.svg);
			bottom: calc(100% - 33px);
			left: 29px;
		}
	}
}


@keyframes btn {
    0%   {box-shadow: 0 0 0 0px $color-green; opacity: .5}
    50%  {box-shadow: 0 0 0 4px $color-green; opacity: .5}
    100% {box-shadow: 0 0 4px 24px $color-green; opacity: 0}
}